import React, { Fragment } from 'react';
import { Station, LineConfig } from '../types';
import { LINE_COLORS } from '../constants';
import { TransferIcon } from './TransferIcon';
import { ArrowLeft, ArrowRight } from 'lucide-react';

// Inline accessible icon to avoid import issues with specific library versions
const AccessibleIcon = ({ size = 14, className = "" }: { size?: number, className?: string }) => (
  <svg
    xmlns="http://www.w3.org/2000/svg"
    width={size}
    height={size}
    viewBox="0 0 24 24"
    fill="none"
    stroke="currentColor"
    strokeWidth="2"
    strokeLinecap="round"
    strokeLinejoin="round"
    className={className}
  >
    <circle cx="12" cy="4" r="2" />
    <path d="M19 17v4" />
    <path d="m15 14 4.3 4.3" />
    <path d="M11 11h4l1.3 2" />
    <path d="M7 17a5 5 0 0 1 5-5h2" />
  </svg>
);

interface StripMapProps {
  config: LineConfig;
  stations: Station[];
}

export const StripMap: React.FC<StripMapProps> = ({ config, stations }) => {
  const mainLineColor = LINE_COLORS[config.lineCode] || LINE_COLORS['DEFAULT'];

  // Theme configuration
  const themeStyles = {
    'modern-black': {
      bg: 'bg-black',
      text: 'text-white',
      line: mainLineColor.bg,
      subText: 'text-gray-300',
      dotBorder: 'border-white',
      arrowColor: 'text-white'
    },
    'vintage-white': {
      bg: 'bg-gray-100',
      text: 'text-slate-900',
      line: mainLineColor.bg,
      subText: 'text-slate-600',
      dotBorder: 'border-gray-800',
      arrowColor: 'text-slate-800'
    },
    'digital-find': {
      bg: 'bg-[#1a1a1a]',
      text: 'text-[#fbbf24]', // Amber text often used in displays
      line: 'bg-yellow-500',
      subText: 'text-blue-300',
      dotBorder: 'border-white',
      arrowColor: 'text-green-500'
    }
  };

  const currentTheme = themeStyles[config.theme];
  const isDigital = config.theme === 'digital-find';
  const isModernVinyl = config.theme === 'modern-black';

  return (
    <div className={`w-full overflow-x-auto custom-scrollbar ${currentTheme.bg} p-8 min-h-[300px] flex items-center justify-center`}>
      <div className="min-w-[1000px] w-full relative select-none">

        {/* Header Information (Left Side) */}
        <div className="absolute left-0 top-0 flex flex-col max-w-[300px] z-20">
          {isModernVinyl ? (
            // Modern Vinyl Header Layout
            <div className="flex flex-col gap-6">
              <div className="flex items-start gap-4">
                <TransferIcon line={config.lineCode} size="2xl" className="shrink-0" />
                <div className="flex flex-col pt-1">
                  <h1 className={`text-4xl font-bold tracking-tight ${currentTheme.text} whitespace-nowrap`}>
                    {config.viaText}
                  </h1>
                  {config.destinations && config.destinations.length > 0 && (
                    <div className={`text-xl font-bold ${currentTheme.text} opacity-90 mt-1`}>
                      {config.destinations.map((dest, i) => (
                        <div key={i}>{dest}</div>
                      ))}
                    </div>
                  )}
                </div>
              </div>

              {config.boroughs && config.boroughs.length > 0 && (
                <div className={`flex flex-col gap-1 ${currentTheme.text} opacity-80 pl-2`}>
                  {config.boroughs.map((b, i) => (
                    <span key={i} className="text-sm font-bold tracking-[0.2em uppercase border-l-2 border-white/30 pl-3">
                      {b.toUpperCase()}
                    </span>
                  ))}
                </div>
              )}
            </div>
          ) : (
            // Standard/Vintage Header Layout
            <div className="flex flex-col">
              <div className="flex items-center gap-3 mb-2">
                <TransferIcon line={config.lineCode} size="xl" />
                <h1 className={`text-4xl font-bold tracking-tight ${currentTheme.text} whitespace-nowrap`}>
                  {config.viaText}
                </h1>
              </div>
              <div className={`text-sm font-semibold uppercase tracking-wider ${currentTheme.subText} mt-1`}>
                {config.directionText}
              </div>
              <div className={`text-xs mt-1 ${currentTheme.subText}`}>
                {config.startText} to {config.endText}
              </div>
            </div>
          )}

          {/* Directional Arrow Left */}
          {!isModernVinyl && (
            <div className={`mt-8 flex items-center gap-2 ${currentTheme.arrowColor} opacity-50`}>
              <ArrowLeft size={48} strokeWidth={3} />
            </div>
          )}
        </div>

        {/* Directional Arrow Right (Far Right) - Hide for Modern Vinyl as it's cleaner */}
        {!isModernVinyl && (
          <div className={`absolute right-0 top-32 flex items-center gap-2 ${currentTheme.arrowColor} opacity-50 z-10`}>
            <ArrowRight size={48} strokeWidth={3} />
          </div>
        )}

        {/* The Main Strip Line */}
        <div className="relative mt-48 ml-[320px] mr-[100px]">
          {/* The physical line background */}
          <div
            className="absolute top-[12px] left-0 w-full h-4 z-0 rounded-full"
            style={{ backgroundColor: currentTheme.line }}
          />

          {/* Stations Container */}
          <div className="flex justify-between items-start relative z-10 w-full px-4">
            {stations.map((station, index) => {
              const isRod = station.isTerminus || station.isNextStop;
              const nextStation = stations[index + 1];
              const showSeparator = isModernVinyl && nextStation && station.borough && nextStation.borough && station.borough !== nextStation.borough;

              return (
                <Fragment key={station.id}>
                  <div className="flex flex-col items-center relative group" style={{ width: `${100 / stations.length}%` }}>

                    {/* Label for Next/Last Stop (Modern Vinyl only) */}
                    {isModernVinyl && station.isNextStop && (
                      <div className="absolute -top-12 text-white font-bold text-xs tracking-widest bg-black px-2 py-1 border border-white rounded animate-pulse">
                        NEXT STOP
                      </div>
                    )}
                    {isModernVinyl && station.isTerminus && (
                      <div className="absolute -top-12 text-white font-bold text-xs tracking-widest bg-black px-2 py-1 border border-white rounded">
                        LAST STOP
                      </div>
                    )}

                    {/* Station Dot / Rod */}
                    <div
                      className={`
                      ${isRod && isModernVinyl ? 'w-16 h-8 rounded-full' : 'w-8 h-8 rounded-full'} 
                      border-[3px] ${currentTheme.dotBorder} z-10 mb-2 transition-all duration-200
                      flex items-center justify-center
                    `}
                      style={{
                        backgroundColor: isDigital ? (station.isTerminus ? '#ef4444' : '#22c55e') : (station.isTerminus || station.isNextStop ? '#fff' : (isModernVinyl ? '#000' : (config.theme === 'vintage-white' ? '#fff' : '#000'))),
                        borderColor: isModernVinyl && (station.isTerminus || station.isNextStop) ? 'white' : undefined,
                        boxShadow: isRod && isModernVinyl ? '0 0 10px rgba(255,255,255,0.5)' : 'none'
                      }}
                    >
                      {/* Inner dot for Rods (optional, to make it look like a highlighted station) */}
                      {isRod && isModernVinyl && (
                        <div className="w-12 h-4 bg-black rounded-full" />
                      )}
                    </div>

                    {/* Station Name (Rotated) */}
                    <div className="absolute top-16 left-1/2 w-[300px] origin-top-left rotate-45 translate-x-[-10px] pointer-events-none z-20">
                      <div className={`text-lg font-bold leading-tight ${currentTheme.text} flex items-center gap-2`}>
                        {station.name}
                        {station.isAccessible && (
                          <AccessibleIcon size={16} className={isDigital ? 'text-blue-400' : 'text-blue-500'} />
                        )}
                      </div>
                    </div>

                    {/* Transfers */}
                    <div className="absolute top-32 left-1/2 -translate-x-1/2 flex flex-col items-center gap-1 min-w-[80px]">
                      {station.transfers.length > 0 && (
                        <div className="flex flex-wrap justify-center gap-1">
                          {station.transfers.map((t, i) => (
                            <TransferIcon key={i} line={t} size="sm" />
                          ))}
                        </div>
                      )}
                      {isDigital && station.transfers.length > 0 && (
                        <span className="text-[10px] uppercase text-blue-400 font-mono mt-1">Transfer</span>
                      )}
                    </div>
                  </div>

                  {/* Borough Separator */}
                  {showSeparator && (
                    <div className="absolute h-24 w-[2px] bg-white rotate-[30deg] z-0 top-[-20px]"
                      style={{
                        left: `calc(${(index + 1) * (100 / stations.length)}% - ${(100 / stations.length) / 2}%)`,
                        height: '140px',
                        top: '-50px',
                        background: 'linear-gradient(to bottom, transparent, white 20%, white 80%, transparent)'
                      }}
                    >
                      {/* Manual "dots" or styling for the separator if needed, but a line is standard */}
                    </div>
                  )}
                </Fragment>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
};
